
html{
  background-color: #030406;
}
.container{
  margin: auto;
  display: grid;
  grid-gap: 15px;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-template-rows: repeat(2, 400px);
}
.container > div{
  margin: auto;
}
@media only screen and (min-width: 900px) {
.container{
    margin: auto;
    width: 900px;
    padding: 10px;
}
.container > div{
    margin: auto;
}
}

